import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from './../../library'
import Curriculum from '../Curriculum/Curriculum'
import Meta from '../Meta/Meta'
import MiniMetaLink from '../MetaLink/MiniMetaLink'


class Avatar extends Component {
   state={
        user:"",
        avatar:"",
        vault:"",
        meta:"",
        folder:"",
        standard:"erc1155"
    }

     
    async setVault(_vault){
       
        await this.setState({vault:_vault})
        
        try{
            let _vault=this.state.vault
            document.getElementById("personalfeed_panel").innerHTML=""
            let ico=""
            if(_vault=="")ico=""
            if(_vault=="visual")ico="🎨"
            if(_vault=="vox")ico="🎲"
            if(_vault=="audio")ico="🎻"
            if(_vault=="video")ico="🎬"
            document.getElementById("personal_mainlabel").textContent=_vault+" "+ico
            
            if((_vault!="")&&(_vault!="meta"))
            document.getElementById("personal_description").style.display="none"
            }catch(e){}
        //this.loadFeed("default",'all',_vault)
        await utils.loadAllPersonalEvents("default",utils.getUrlVars()["account"],_vault,this.state.standard,document.getElementById("personalfeed_panel"))
    }


    async setStandard(stn){
        await this.setState({standard:stn})
        document.getElementById("personalfeed_panel").innerHTML=""
        await utils.loadAllPersonalEvents("default",utils.getUrlVars()["account"],this.state.vault,stn,document.getElementById("personalfeed_panel"))
    }


    async componentDidMount() {
        let _folder
       try{ utils.loadShopColors(getUrlVars()["account"])
       document.getElementById("personal_name").textContent=await utils.loadMetaParam(getUrlVars()["account"],"name")
       let desc=await utils.loadMetaParam(getUrlVars()["account"],"description")
       _folder=await utils.loadMetaParam(getUrlVars()["account"],"folder")
       document.getElementById("personal_description").textContent=desc.replaceAll("<br>","\n")
    }catch(e){}
      this.setState({folder:_folder,meta:await utils.metadata(getUrlVars()["account"],getUrlVars()["account"])})
       await this.loadBlockchainData()

       document.getElementById("Avatar_face_container").innerHTML=""
       let avat = document.createElement('img')
       //alert(utils.uri2url(await utils.loadMetaParam(getUrlVars()["account"],"image")))
        avat.setAttribute('src',utils.uri2url(await utils.loadMetaParam(getUrlVars()["account"],"image")))
        avat.setAttribute('class',"avatar_banner")
        document.getElementById("Avatar_face_container").appendChild(avat)
    }
    
    
        async loadBlockchainData() {
            let account
            let _user
            await window.ethereum.enable().then(async function (accounts) {
                account=accounts[0];
                _user=accounts[0]
                if(getUrlVars()['account'])account=getUrlVars()['account']
                if(account=="undefined")account=accounts[0]
                //window.history.pushState('avatar_page', 'Avatar Display', '/cyphervault/?account='+account)
                const { REACT_APP_ABI } = process.env;
                const { REACT_APP_BOWS } = process.env;
                const beans = new web3.eth.Contract(JSON.parse(REACT_APP_ABI), REACT_APP_BOWS) 
          
                if(account!=""){   
                    let avatar="👻";
                    let res=account
                    avatar=utils.getAvatar(res.toLowerCase());
                    document.getElementById("Avatar_face_container").innerHTML = '<span class="Avatar_face" aria-labelledby="jsx-a11y/accessible-emoji" role="img">'+avatar+'</span>';
             
                  
               }else{
                    document.getElementById("Avatar_face_container").innerHTML = '<span class="Avatar_face" aria-labelledby="jsx-a11y/accessible-emoji" role="img">👻</span>';             
                }
            })
            this.setState({user:_user,avatar:account})
        }


          
           MetaList = () => {
            let listItems
            if(this.state.folder) listItems = this.state.folder.split(",").map((addr) =>
              // Correct! Key should be specified inside the array.
              <MiniMetaLink address={addr}/>
          
            );
            return (
              <>
                {listItems}
              </>
            );
          }
          
          
          
        



    render(){
        return ( 
            <>
            
       
            <div className="avatar_left_col">
                {(this.state.vault!="meta")?
                    <>
                        <div id="personal_header">
                            <div id="personal_mainlabel"></div>
                            {(this.state.vault!="")?
                                <>
                                <div className={(this.state.standard=="item")?"personal_setstandard_button dark darkblue":"personal_setstandard_button"} onClick={() => this.setStandard("item")}>ITEMS</div>
                                <div className={(this.state.standard=="erc1155")?"personal_setstandard_button dark darkblue":"personal_setstandard_button"} onClick={() => this.setStandard("erc1155")}>ERC1155</div>
                                </>
                                :
                                <></>
                            }
                        </div>
                        <div className="clear"></div>
                        <div id="personal_description" className="personal_description"></div>
                        <div className="personalfeed_panel" id="personalfeed_panel">
                        </div>
                    </>
                :
                <>
                <div className="personalfeed_panel" id="personalfeed_panel">
                </div>
                
                <Meta></Meta>
                </>
                }
            <div className="clear"></div>
            
            </div>
            <div className="cardpanel">
            <div id="Avatar_face_container" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => window.open('https://etherscan.com/address/'+getUrlVars()["account"])}></div>
            <div className="clear"></div>
            <div id="personal_name" className="Avatar_panel"></div>
            <div className="clear"></div>
            {(getUrlVars()["account"]==this.state.user)?
            <>
            <div className="personalmenu darkborder">
            <div className="personal_meta" onClick={() => this.setVault("meta")}>💄</div>
            <div className="personal_meta" onClick={() => window.open("https://ipfs.io/ipfs/"+this.state.meta)}>✨</div>
            </div>
            <div className="clear"></div>
            </>
            :
            <></>
            }
           
           <div id="Avatar_panel" className="Avatar_panel ">
            {getUrlVars()["account"]}
            
        </div>
        <>
        
        
        
        <div className="clear"/>
        
        <>
        <div className="personal_sectionbutton dark darkblue" onClick={() => this.setVault("visual")}>🎨 VISUAL</div>
        <div className="personal_sectionbutton dark darkblue" onClick={() => this.setVault("vox")}>🎲 VOX</div>
        <div className="personal_sectionbutton dark darkblue" onClick={() => this.setVault("audio")}>🎻 AUDIO</div>
        <div className="personal_sectionbutton dark darkblue" onClick={() => this.setVault("video")}>🎬 VIDEO</div>
        </>
        
        <div id="factory_flyer">     
        </div>
        </>
        <div className="links_label text white">Links</div>
        {this.MetaList()}
        </div>
        
        </>
        );
    }


}

function getAvatar(res){
    let avatar="👻";
    if(res.slice(-1)==="0")avatar="🐶";
    if(res.slice(-1)==="1")avatar="🐱";
    if(res.slice(-1)==="2")avatar="🐹";
    if(res.slice(-1)==="3")avatar="🐸";
    if(res.slice(-1)==="4")avatar="🐵";
    if(res.slice(-1)==="5")avatar="🐻";
    if(res.slice(-1)==="6")avatar="🐼";
    if(res.slice(-1)==="7")avatar="🐰";
    if(res.slice(-1)==="8")avatar="🐨";
    if(res.slice(-1)==="9")avatar="🐯";
    if(res.slice(-1)==="a")avatar="🦁";
    if(res.slice(-1)==="b")avatar="🐮";
    if(res.slice(-1)==="c")avatar="🐷";
    if(res.slice(-1)==="d")avatar="🐺";
    if(res.slice(-1)==="e")avatar="🐗";
    if(res.slice(-1)==="f")avatar="🐔";
    return avatar;
}

function getFamily(ress){
    
    let avatar="☠️";
    let res=ress.slice(-2)
    res = res.substring(0, 1);
    if(res.slice(-1)==="0")avatar="🐳";
    if(res.slice(-1)==="1")avatar="🦈";
    if(res.slice(-1)==="2")avatar="🐅";
    if(res.slice(-1)==="3")avatar="🐍";
    if(res.slice(-1)==="4")avatar="🐞";
    if(res.slice(-1)==="5")avatar="🐪";
    if(res.slice(-1)==="6")avatar="🦋";
    if(res.slice(-1)==="7")avatar="🐙";
    if(res.slice(-1)==="8")avatar="🐄";
    if(res.slice(-1)==="9")avatar="🐇";
    if(res.slice(-1)==="a")avatar="🐖";
    if(res.slice(-1)==="b")avatar="🐏";
    if(res.slice(-1)==="c")avatar="🐐";
    if(res.slice(-1)==="d")avatar="🦎";
    if(res.slice(-1)==="e")avatar="🐠";
    if(res.slice(-1)==="f")avatar="🦍";
    return avatar;
}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

// <PersonalFeed update={this.props.update} vault={this.state.vault} avatar={this.state.avatar}></PersonalFeed>
           

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Avatar